@import url(https://fonts.googleapis.com/css?family=Exo+2&display=swap);
:root {
  --primary-color: #0dafd8;
  --dark-color: #333;
  --black-color: #222;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Exo 2", sans-serif;
  line-height: 1.6;
  overflow: hidden;
}

.container {
  width: 100vw;
  height: 100vh;
  margin: auto;
  padding: 3rem 3rem;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  opacity: 0.7;
}

ul {
  list-style: none;
}

.list-item {
  margin-bottom: 0.5rem;
  padding: 0.3rem 1rem;
}

.btn {
  display: inline-block;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  outline: none;
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
}

.btn:hover {
  opacity: 0.7;
}

.my-1 {
  margin: 1rem 0;
}

.center {
  text-align: center;
}

.ttc {
  text-transform: capitalize;
}

.ttu {
  text-transform: uppercase;
}

nav {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  z-index: 1;
}

nav ul {
  display: flex;
}

nav a {
  padding: 0.45rem;
  margin: 0 0.25rem;
  font-size: 1.2rem;
}

.sub-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2rem 1rem;
  font-size: 0.9rem;
}

.sub-nav :nth-child(n) {
  padding: 0.2rem;
}

.auth-div {
  cursor: pointer;
}

.banner-title {
  margin-bottom: 2rem;
}

.log-page {
  overflow-y: scroll;
  padding-bottom: 10rem;
}

.log-list {
  max-width: 25rem;
  margin: 2rem auto;
  border-radius: 5px;
}

.post-form {
  width: 70vw;
  max-width: 650px;
  margin: auto;
  padding: 1.5rem;
  border: 2px solid #888;
}

.post-form h2 {
  margin-bottom: 2rem;
}

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: none;
  border-bottom: 1px solid #ccc;
}

input[type="submit"],
.post-form button {
  font: inherit;
  display: block;
  width: 100%;
  margin-top: 2rem;
  color: inherit;
}

.log-details {
  border-radius: 5px;
  margin-bottom: 1.5rem;
  word-break: break-all;
}

.log-details h3 {
  margin-top: 0.5rem;
}

.delete-btn {
  color: rgb(177, 112, 69);
  margin: 0.5rem 0;
  cursor: pointer;
  display: inline-block;
}

@media screen and (max-width: 500px) {
  nav a {
    font-size: 1rem;
  }

  .log-page,
  .post-form,
  .sub-nav {
    font-size: 0.7rem;
  }
}

